* {
  padding: 0;
  margin: 0;
}

#backgroundImage {
  width: 100vw;
  height: auto;
  background-color: #DAD5EA;
  background-image: url("../img/kampusuwb.jpg");
  display: inline-block;
  position: relative;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
input {
  background-color: white;
  border: 2px solid black;
  color: black;
  border-radius: 25px;
  height: 20px;
  width: 200px;
  &:active {
    border: 2px solid white;
    background-color: #094785;
    color: white;
  }
}
.container {
  width: 1000px;
  position: relative;
  height: auto;
}

footer {
  opacity: 0.85;
  margin-top: 50px;
  display: inline-block;
  position: sticky;
  border: 2px solid #262626;
  width: 100vw;
  height: 3rem;
  background-color: #094785;
  font-size: 10px;
  color: white;
  font-weight: 100;
  text-align: center;
  font-family: sans-serif;
  text{

    font-size: 10px;
    color: white;
    font-weight: 100;
    text-align: center;
    font-family: sans-serif;
    padding-top: 2.5vh;
    display: inline-block;
  }
}

.container {
  width: 90vw;
  margin: 0 auto;
}

.Menu {
  opacity: 0.85;
  width: 100vw;
  height: 4.3rem;
  margin-right: 15%;
  background-color: #094785;
  border: 2px solid #262626;
  position: fixed;
  z-index: 5
}

.MenuList {
  list-style: none;
  display: flex;
  padding-left: 20%;
  position: fixed;
  transition: 0.5s all;
}

.MenuButton {
  display: inline-block;
  border-radius: 10px;
  margin-top: 1rem;
  margin-left: 1.5rem;
  color: black;
  height: 2rem;
  width: 10rem;
  background-color: white;
  border: 1.5px solid white;
  font-size: 1.2rem;
  font-weight: 100;
  text-align: center;
  line-height: 2rem;
  font-family: 'Baloo Bhaijaan', cursive;
  text-decoration: none;

  &:hover {
    background-color: #1D028C;
    color: white;
    transition: 0.5s all;
  }
}

.styleList {
  display: inline-block;
  border-radius: 10px;
  margin-top: 1rem;
  margin-left: 85%;
  color: black;
  height: 2rem;
  width: 2rem;
  background-color: white;
  border: 1.5px solid white;
  font-size: 1.2rem;
  font-weight: 100;
  text-align: center;
  line-height: 2rem;
  font-family: 'Baloo Bhaijaan', cursive;
  text-decoration: none;

  > li {
    list-style: none;
    display: none;
    width: 100px;
    height: 25px;
    background-color: white;
    border: 1px solid black;
  }

  &:hover {
    background-color: #1D028C;
    color: white;
    transition: 0.5s all;

    > li {
      margin-top: 5px;
      list-style: none;
      display: flex;
      float: left;
      width: 50px;
      height: 25px;
      font-size: 14px;
      font-weight: 100;
      background-color: white;
      border: 1px solid black;
      transition: 1s all;
    }
  }
}

.infoBox {
  display: flex;
  float: left;
  background-color: white;
  display: flex;
  margin-left: 15%;
  margin-top: 10%;
  width: 800px;
  height: 600px;
  border: 5px solid black;
  border-radius: 10px;

  &:hover {
    background-color: #094785;
    color: white;
    transition: 0.5s all;
    border: 5px solid white;
  }
}

.infoBoxCircle {
  border: 2px solid black;
  border-radius: 50%;
  display: flex;
  width: 120px;
  height: 120px;
  margin-left: 30%;
  margin-top: 15%;
  margin-bottom: 15%;
  border-style: dotted;
  text-align: center;
  font-weight: 400;
  font-size: 16px;
}

.infoBoxInnerText {
  margin-top: 155%;
  display: flex;
  text-align: center;
  font-weight: 400;
  font-size: 16px;
}

.infoBoxUwB {
  display: flex;
  float: left;
  background-color: white;
  display: flex;
  margin-left: 5%;
  margin-top: 10%;
  width: 300px;
  height: 400px;
  border: 5px solid black;
  border-radius: 10px;
  text-decoration: none;

  &:hover {
    background-color: #B40D0D;
    color: white;
    transition: 0.5s all;
    border: 5px solid white;
  }
}

.infoBoxUwB .infoBoxCircle {
  box-shadow: 0px 0px 10px 10px #888888;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../img/w_uwb_kolor.jpg");
  border: 2px solid white;
}

.infoBoxPB {
