  width: 700px;
  height: 400px;
  border: 5px solid black;
  margin-left: 22.5%;
  background: white;
  border-radius: 5px;
}

.contactCircle {
  display: inline-block;
  height: 150px;
  width: 150px;
  border-radius: 50%;
  border: 3px solid black;
  margin-left: 40%;
  margin-top: 5%;
}

.contactInfo {
  font-size: 20px;
  text-align: center;
  padding-top: 15px;
}

.infoBoxPanel {
  display: flex;
  float: left;
  background-color: white;
  display: flex;
  margin-left: 5%;
