}

.StudentPanel {
  position: relative;
  display: block;
  width: 100%;
  height: 10vh;
  color: white;
  font-weight: 400;
  background-color: hotpink;
  text-align: center;
  clear: both;
  z-index: 3;

  &:hover {
    transition: 0.5s all;
    padding-top: 30%;
    background-color: black;
    color: red;
  }
}

.StudentDescription {
